import {Component, EventEmitter, Output} from "@angular/core";
import {FileAnalyticsFileShareAccessData} from "@generated/file-analytics-file-share-access-data";
import {FileAnalyticsDashboardService} from "@generated/file-analytics-dashboard-service";
import {ManagedObject} from "@service/managed-object";
import {RefreshHandler} from "@service/global-refresh.service";
import {BaseFileShareDashboardComponent} from "./base-file-share-dashboard.component";
import {FileAnalyticsFileSharesListType} from "@generated/file-analytics-file-shares-list-type";
import {IllustratedMessageType} from "@component/illustrated-message/illustrated-message.component";
import {DateFormat} from '@util/date-time';

@Component({
   templateUrl: "file-analytics-file-share-top-accessed.html",
   styleUrls: ["file-analytics-file-share-top-accessed.scss", "../file-analytics-common.scss"],
   selector: "vsan-file-analytics-file-share-top-accessed",
})
export class FileAnalyticsFileShareTopAccessedComponent extends BaseFileShareDashboardComponent {
   IllustratedMessageType: typeof IllustratedMessageType = IllustratedMessageType;
   DateFormat: typeof DateFormat = DateFormat;

   @Output()
   viewMoreCliked: EventEmitter<FileAnalyticsFileSharesListType> = new EventEmitter<FileAnalyticsFileSharesListType>();

   loading: boolean = true;
   errorMessage: string;
   shares: FileAnalyticsFileShareAccessData[];

   constructor(private fileAnalyticsDashboardService: FileAnalyticsDashboardService) {
      super();
   }

   @RefreshHandler
   async fetch(): Promise<void> {
      this.loading = true;
      this.errorMessage = null;
      try {
         this.shares = await this.fileAnalyticsDashboardService.queryTopAccessedFileShares(
               ManagedObject.contextObject, this.dateRange.from, this.dateRange.to);
      } catch (e) {
         this.errorMessage = e;
      } finally {
         this.loading = false;
      }
   }

   navigateToFileShares(): void {
      this.viewMoreCliked.emit(FileAnalyticsFileSharesListType.TOP_ACCESSED);
   }
}
