import {
   AfterViewInit,
   ChangeDetectorRef,
   Component,
   EventEmitter,
   Input,
   Output,
   QueryList,
   ViewChildren} from "@angular/core";
import {DraggableCardDirective} from "@component/drag-and-drop/draggable-card.directive";
import {CardContainer, DragCardPosition} from "@component/drag-and-drop/drag-card-position";
import {CardName, CardView} from "@component/drag-and-drop/card-view";
import {DateRangeData} from "@component/datetime-picker/date-range-picker.component";
import {FileAnalyticsFileSharesListType} from "@generated/file-analytics-file-shares-list-type";

@Component({
   templateUrl: "file-share-dashboard-chart-view.html",
   styleUrls: ["file-share-dashboard-chart-view.scss"],
   selector: "vsan-file-share-dashboard-chart-view",
})
export class FileShareDashboardChartViewComponent implements AfterViewInit {
   // File analytics "File share dashboard" cards
   readonly FILE_ANALYTICS_FILE_SHARE_CAPACITY_TRENDING: string = "fileAnalyticsFileShareCapacityTrending";
   readonly FILE_ANALYTICS_FILE_SHARE_FASTEST_GROWING: string = "fileAnalyticsFastestGrowingFileShares";
   readonly FILE_ANALYTICS_FILE_SHARE_LARGEST: string = "fileAnalyticsLargestFileShares";
   readonly FILE_ANALYTICS_FILE_SHARE_TOP_ACCESSED: string = "fileAnalyticsTopAccessedFileShares";
   readonly FILE_ANALYTICS_FILE_SHARE_TOP_QUOTA_USAGE: string = "FileAnalyticsFileShareTopQuotaUsage";

   @Input()
   dateRange: DateRangeData;

   @Output()
   viewMoreCliked: EventEmitter<FileAnalyticsFileSharesListType> = new EventEmitter<FileAnalyticsFileSharesListType>();

   // The service cards are defined inside templates, so they can be dragged
   @ViewChildren(DraggableCardDirective)
   dashboardTemplates: QueryList<DraggableCardDirective>;

   cardView: CardView;
   FileAnalyticsFileSharesListType: typeof FileAnalyticsFileSharesListType = FileAnalyticsFileSharesListType;

   constructor(private cdRef: ChangeDetectorRef) {
      this.cardView = new CardView(CardName.FILE_SHARE_DASHBOARD_ANALYTICS, this.getFileShareDashboardCards);
   }

   ngAfterViewInit(): void {
      // For some reason the templates cannot be displayed without this manual change detection.
      this.cdRef.detectChanges();
   }

   showSharesListView(type: FileAnalyticsFileSharesListType): void {
      this.viewMoreCliked.emit(type);
   }

   private getFileShareDashboardCards(): Map<string, DragCardPosition> {
      const result: Map<string, DragCardPosition> = new Map<string, DragCardPosition>();
      result.set(this.FILE_ANALYTICS_FILE_SHARE_CAPACITY_TRENDING,
            new DragCardPosition(CardContainer.LEFT, 0));
      result.set(this.FILE_ANALYTICS_FILE_SHARE_FASTEST_GROWING,
            new DragCardPosition(CardContainer.LEFT, 1));
      result.set(this.FILE_ANALYTICS_FILE_SHARE_LARGEST,
            new DragCardPosition(CardContainer.RIGHT, 0));
      result.set(this.FILE_ANALYTICS_FILE_SHARE_TOP_ACCESSED,
            new DragCardPosition(CardContainer.RIGHT, 1));
      result.set(this.FILE_ANALYTICS_FILE_SHARE_TOP_QUOTA_USAGE,
            new DragCardPosition(CardContainer.RIGHT, 2));
      return result;
   }
}
