import {Component} from "@angular/core";
import {FileAnalyticsDashboardService} from "@generated/file-analytics-dashboard-service";
import {FileAnalyticsShareCapacityTrending} from "@generated/file-analytics-share-capacity-trending";
import {ManagedObject} from "@service/managed-object";
import {RefreshHandler} from "@service/global-refresh.service";
import {ColumnLineChartData} from "@component/chart/column-line-chart/column-line-chart.data";
import {FileAnalyticsChartService} from "../file-analytics-chart.service";
import {FileAnalyticsUtil} from "../file-analytics-util";
import {VsanUiUtils} from "@util/vsan-util";
import {IllustratedMessageType} from "@component/illustrated-message/illustrated-message.component";
import {BaseFileShareDashboardComponent} from "./base-file-share-dashboard.component";

@Component({
   templateUrl: "file-analytics-file-share-capacity-trending.html",
   styleUrls: ["file-analytics-file-share-capacity-trending.scss", "../file-analytics-common.scss"],
   selector: "vsan-file-analytics-file-share-capacity-trending",
})
export class FileAnalyticsFileShareCapacityTrendingComponent extends BaseFileShareDashboardComponent {
   FileAnalyticsUtil: typeof FileAnalyticsUtil = FileAnalyticsUtil;
   IllustratedMessageType: typeof IllustratedMessageType = IllustratedMessageType;

   // group the series data of the chart columns
   columns: ColumnLineChartData[] = [];

   // group the series data of the lines
   lines: ColumnLineChartData[] = [];

   chartSubTitle: string;
   categories: string[];
   errorMessage: string;
   loading: boolean;

   constructor(private fileAnalyticService: FileAnalyticsDashboardService,
         private fileAnalyticsChartService: FileAnalyticsChartService) {
      super();
   }

   @RefreshHandler
   async fetch(): Promise<void> {
      try {
         this.reset();
         this.prepareChart();
         const result: FileAnalyticsShareCapacityTrending[] = await this.fileAnalyticService
               .queryFileShareCapacityTrending(
                     ManagedObject.contextObject, this.dateRange.from, this.dateRange.to);

         this.categories = this.getChartCategoriesFromReportData(result);
         const seriesData: ColumnLineChartData[] = this.fileAnalyticsChartService.parseCapacityTrendingData(result);
         if (!VsanUiUtils.isEmpty(seriesData)) {
            this.columns = this.getColumnKeys().map(column => seriesData.find(data => data.id === column));
            this.lines = this.getLineKeys().map(line => seriesData.find(data => data.id === line));
         }
      } catch (err) {
         this.errorMessage = err;
      } finally {
         this.loading = false;
      }
   }

   private reset(): void {
      this.loading = true;
      this.errorMessage = null;
      this.columns = [];
      this.lines = [];
   }

   /**
    * Get the xAxis categories from the dates in the result
    */
   private getChartCategoriesFromReportData(reportData: FileAnalyticsShareCapacityTrending[]): string[] {
      const categories: number[] = [];
      for (const report of reportData) {
         categories.push(report.date);
      }
      return FileAnalyticsUtil.getCategoryDates(categories);
   }

   private prepareChart(): void {
      this.chartSubTitle = FileAnalyticsUtil.getChartSubTitleFromDateRange(this.dateRange);
   }

   /**
    * Holds the properties that are displayed as columns in the chart
    */
   private getColumnKeys(): string[] {
      return [
         FileAnalyticsChartService.CAPACITY_ADDED_PROPERTY,
         FileAnalyticsChartService.CAPACITY_REMOVED_PROPERTY,
      ];
   }

   /**
    * Holds the properties that are displayed as lines in the chart
    */
   private getLineKeys(): string[] {
      return [FileAnalyticsChartService.TOTAL_CAPACITY_PROPERTY];
   }
}
