import {Component} from "@angular/core";
import {DateRangeData} from "@component/datetime-picker/date-range-picker.component";
import {FileAnalyticsFileSharesListType} from "@generated/file-analytics-file-shares-list-type";
import {VsanUiUtils} from "@util/vsan-util";
import {PickerType} from "@component/datetime-picker/picker-type";

@Component({
   templateUrl: "file-share-dashboard.html",
   styleUrls: ["file-share-dashboard.scss"],
   selector: "vsan-file-share-dashboard",
})
export class FileShareDashboardComponent {
   readonly FILE_ANALYTICS_DATEPICKER_INTERVAL: number = 7;

   PickerType: typeof PickerType = PickerType;
   reportType: FileAnalyticsFileSharesListType;
   dateRange: DateRangeData;
   signpostContent: string;
   viewMode: ViewMode = ViewMode.CHART;
   ViewMode: typeof ViewMode = ViewMode;

   showSharesListView(reportType: FileAnalyticsFileSharesListType): void {
      this.reportType = reportType;
      this.viewMode = ViewMode.LIST;
      this.signpostContent =
            VsanUiUtils.getString("vsan.cluster.monitor.file.analytics.share.list.date.picker.signpost.content");
   }

   showChartView(): void {
      this.reportType = null;
      this.viewMode = ViewMode.CHART;
      this.signpostContent = null;
   }
}

export enum ViewMode {
   CHART, LIST,
}
