import {VsanUiUtils} from "@util/vsan-util";
import {DateRangeData} from "@component/datetime-picker/date-range-picker.component";
import {FileOperationType} from "@generated/file-operation-type";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {DateFormat} from "@util/date-time";
import {FileAnalyticsShareData} from "@generated/file-analytics-share-data";
import {FileAnalyticsShareFileData} from "@generated/file-analytics-share-file-data";

/**
 * Holds the common methods that are used among the file analytics charts
 */
export class FileAnalyticsUtil {
   /**
    * Format the size number into readable size string, and keep 2 digits.
    */
   static fileSizeLabelFormatter(point: any): string {
      if (point.value === 0) {
         return point.value.toString();
      }
      return VsanUiUtils.formatFileSize(point.value);
   }

   /**
    * Get the chart subtitle according to the date range.
    * If the range is from today, then the subtitle is 'Showing data for the last x days',
    * otherwise it's 'Showing data from x to y'
    */
   static getChartSubTitleFromDateRange(dateRange: DateRangeData): string {
      const numOfDays: number = dateRange.getNumberOfDays();

      if (dateRange.isStartingFromToday()) {
         return VsanUiUtils.getString(
               "vsan.cluster.monitor.file.analytics.file.share.fastest.growing.chart.lastdays.title", numOfDays);
      } else {
         const dateFrom: string =
               VsanDateTimeUtils.applyFormatting(new Date(dateRange.from), DateFormat.DAY_ONLY);
         const dateTo: string =
               VsanDateTimeUtils.applyFormatting(new Date(dateRange.to), DateFormat.DAY_ONLY);
         return VsanUiUtils.getString(
               "vsan.cluster.monitor.file.analytics.file.share.fastest.growing.chart.customdays.title",
               dateFrom, dateTo);
      }
   }

   /**
    * Using the data from the report, extract the unique dates and sort them ascending.
    * Converts to localized date format with Day only portion.
    */
   static getCategoryDates(categories: number[]): string[] {
      return categories
            .filter((v, i, a) => a.findIndex(t => t === v) === i)
            .sort((a, b) => a - b)
            .map(category => VsanDateTimeUtils.applyFormatting(new Date(category), DateFormat.DAY_ONLY));
   }

   static getOperationLabel(operation: FileOperationType): string {
      switch (operation) {
         case FileOperationType.DELETE:
            return VsanUiUtils.getString("vsan.cluster.monitor.file.analytics.share.list.operation.delete");
         case FileOperationType.READ:
            return VsanUiUtils.getString("vsan.cluster.monitor.file.analytics.share.list.operation.read");
         case FileOperationType.WRITE:
            return VsanUiUtils.getString("vsan.cluster.monitor.file.analytics.share.list.operation.write");
         case FileOperationType.RENAME:
            return VsanUiUtils.getString("vsan.cluster.monitor.file.analytics.share.list.operation.rename");
         default:
            return VsanUiUtils.getString("vsan.cluster.monitor.file.analytics.share.list.operation.unknown");
      }
   }

   static getOperationDateTime(fileShare: FileAnalyticsShareData | FileAnalyticsShareFileData): string {
      // The backend doesn't have time information for deleted files, so keep only date portion
      if (fileShare.lastOperation === FileOperationType.DELETE) {
         return VsanDateTimeUtils.applyFormatting(fileShare.lastOperationTime, DateFormat.SHORT_DATE);
      } else {
         return VsanDateTimeUtils.applyFormatting(fileShare.lastOperationTime, DateFormat.FULL_DATE_TIME);
      }
   }
}
