import {StringComparator} from "@component/datagrid/comparator/string-comparator";
import {PipeComparator} from "@component/datagrid/comparator/pipe-comparator";
import {FileShareProtocolPipe} from "@pipe/file-share-protocol-pipe";
import {NumericComparator} from "@component/datagrid/comparator/numeric-comparator";
import {ObjectFilter} from "@component/datagrid/filter/object-filter";
import {FileAnalyticsShareData} from "@generated/file-analytics-share-data";
import {FileAnalyticsShareFileData} from "@generated/file-analytics-share-file-data";

export class FileShareGridUtil {
   static readonly SHARE_NAME_PROPERTY: string = "shareName";
   static readonly SHARE_PROTOCOL_PROPERTY: string = "protocol";
   static readonly SHARE_CURRENT_USAGE_PROPERTY: string = "currentUsage";
   static readonly SHARE_USAGE_PCT_PROPERTY: string = "usagePercentage";
   static readonly SHARE_ACCESS_COUNT_PROPERTY: string = "accessCount";
   static readonly SHARE_GROWTH_PROPERTY: string = "growth";
   static readonly SHARE_GROWTH_PERTENTAGE_PROPERTY: string = "growthPercentage";
   static readonly SHARE_LAST_OPERATION_PROPERTY: string = "lastOperation";
   static readonly SHARE_LAST_OPERATION_TIME_PROPERTY: string = "lastOperationTime";
   static readonly FILE_NAME_PROPERTY: string = "fileName";
   static readonly FILE_LOCATION_PROPERTY: string = "fileLocation";

   static shareNameComparator: StringComparator<FileAnalyticsShareData> =
         new StringComparator<FileAnalyticsShareData>(FileShareGridUtil.SHARE_NAME_PROPERTY);
   static protocolComparator: PipeComparator<FileAnalyticsShareData> =
         new PipeComparator<FileAnalyticsShareData>(
               new FileShareProtocolPipe(), FileShareGridUtil.SHARE_PROTOCOL_PROPERTY);
   static usageComparator: NumericComparator<FileAnalyticsShareData> =
         new NumericComparator<FileAnalyticsShareData>(FileShareGridUtil.SHARE_CURRENT_USAGE_PROPERTY);
   static usagePercentComparator: NumericComparator<FileAnalyticsShareData> =
         new NumericComparator<FileAnalyticsShareData>(FileShareGridUtil.SHARE_USAGE_PCT_PROPERTY);
   static accessCountComparator: NumericComparator<FileAnalyticsShareData> =
         new NumericComparator<FileAnalyticsShareData>(FileShareGridUtil.SHARE_ACCESS_COUNT_PROPERTY);
   static growthComparator: NumericComparator<FileAnalyticsShareData> =
         new NumericComparator<FileAnalyticsShareData>(FileShareGridUtil.SHARE_GROWTH_PROPERTY);
   static growthPercentComparator: NumericComparator<FileAnalyticsShareData> =
         new NumericComparator<FileAnalyticsShareData>(FileShareGridUtil.SHARE_GROWTH_PERTENTAGE_PROPERTY);
   static lastOperationComparator: StringComparator<FileAnalyticsShareData> =
         new StringComparator<FileAnalyticsShareData>(FileShareGridUtil.SHARE_LAST_OPERATION_PROPERTY);
   static lastOperationTimeComparator: NumericComparator<FileAnalyticsShareData> =
         new NumericComparator<FileAnalyticsShareData>(FileShareGridUtil.SHARE_LAST_OPERATION_TIME_PROPERTY);
   static fileNameComparator: StringComparator<FileAnalyticsShareFileData> =
         new StringComparator<FileAnalyticsShareFileData>(FileShareGridUtil.FILE_NAME_PROPERTY);
   static fileLocationComparator: StringComparator<FileAnalyticsShareFileData> =
         new StringComparator<FileAnalyticsShareFileData>(FileShareGridUtil.FILE_LOCATION_PROPERTY);

   static shareNameFilter: ObjectFilter<FileAnalyticsShareData> =
         new ObjectFilter<FileAnalyticsShareData>(FileShareGridUtil.SHARE_NAME_PROPERTY);
   static lastOperationFilter: ObjectFilter<FileAnalyticsShareData> =
         new ObjectFilter<FileAnalyticsShareData>(FileShareGridUtil.SHARE_LAST_OPERATION_PROPERTY);
   static fileNameFilter: ObjectFilter<FileAnalyticsShareFileData> =
         new ObjectFilter<FileAnalyticsShareFileData>(FileShareGridUtil.FILE_NAME_PROPERTY);
   static fileLocationFilter: ObjectFilter<FileAnalyticsShareFileData> =
         new ObjectFilter<FileAnalyticsShareFileData>(FileShareGridUtil.FILE_LOCATION_PROPERTY);
}