import {Component, Input, OnChanges} from "@angular/core";
import {FileAnalyticsShareData} from "@generated/file-analytics-share-data";
import {FileAnalyticsDashboardService} from "@generated/file-analytics-dashboard-service";
import {ManagedObject} from "@service/managed-object";
import {FileAnalyticsShareFileData} from "@generated/file-analytics-share-file-data";
import {FileShareGridUtil} from "./file-share-grid.util";
import {FileAnalyticsUtil} from "../file-analytics-util";

@Component({
   selector: "vsan-file-analytics-share-files",
   templateUrl: "file-analytics-share-files.html",
   styleUrls: ["file-analytics-share-files.scss"],
})
export class FileAnalyticsShareFilesComponent implements OnChanges {

   @Input()
   share: FileAnalyticsShareData;

   files: FileAnalyticsShareFileData[];
   loading: boolean = true;
   errorMessage: string;

   FileShareGridUtil: typeof FileShareGridUtil = FileShareGridUtil;
   FileAnalyticsUtil: typeof FileAnalyticsUtil = FileAnalyticsUtil;

   constructor(private fileAnalyticsDashboardService: FileAnalyticsDashboardService) {
   }

   async ngOnChanges(): Promise<void> {
      if (!this.share) {
         return;
      }
      try {
         this.loading = true;
         this.files = null;
         this.files = await this.fileAnalyticsDashboardService.queryFileShareFiles(
               ManagedObject.contextObject, this.share.uuid);
      } catch (e) {
         this.errorMessage = e;
      } finally {
         this.loading = false;
      }
   }
}