import {Component, EventEmitter, Output} from "@angular/core";
import {FileAnalyticsDashboardService} from "@generated/file-analytics-dashboard-service";
import {ManagedObject} from "@service/managed-object";
import {RefreshHandler} from "@service/global-refresh.service";
import {ChartSectionData} from "@component/chart/model/chart-section.data";
import {VsanCapacityService} from "@component/capacity/capacity.service";
import {SpaceEfficiencyService} from "@service/space-efficiency-service";
import {FileAnalyticsChartService} from "../file-analytics-chart.service";
import {BaseFileShareDashboardComponent} from "./base-file-share-dashboard.component";
import {FileAnalyticsFileShareQuotaOccupancyData} from "@generated/file-analytics-file-share-quota-occupancy-data";
import {FileAnalyticsFileSharesListType} from "@generated/file-analytics-file-shares-list-type";
import {IllustratedMessageType} from "@component/illustrated-message/illustrated-message.component";

@Component({
   templateUrl: "file-analytics-file-share-top-quota-usage.html",
   styleUrls: ["file-analytics-file-share-top-quota-usage.scss", "../file-analytics-common.scss"],
   selector: "vsan-file-analytics-file-share-top-quota-usage",
   providers: [VsanCapacityService, SpaceEfficiencyService],
})
export class FileAnalyticsFileShareTopQuotaUsageComponent extends BaseFileShareDashboardComponent {
   IllustratedMessageType: typeof IllustratedMessageType = IllustratedMessageType;

   @Output()
   viewMoreCliked: EventEmitter<FileAnalyticsFileSharesListType> = new EventEmitter<FileAnalyticsFileSharesListType>();

   loading: boolean = true;
   errorMessage: string;
   shares: FileAnalyticsFileShareQuotaOccupancyData[];
   sections: Map<string, ChartSectionData[]> = new Map<string, ChartSectionData[]>();

   constructor(private fileAnalyticsDashboardService: FileAnalyticsDashboardService,
         private fileAnalyticsChartService: FileAnalyticsChartService) {
      super();
   }

   @RefreshHandler
   async fetch(): Promise<void> {
      this.loading = true;
      this.errorMessage = null;
      try {
         this.shares = await this.fileAnalyticsDashboardService.queryTopQuotaOccupancyFileShares(
               ManagedObject.contextObject, this.dateRange.from, this.dateRange.to);
         // Parse the file share models into chart data series
         this.sections = this.fileAnalyticsChartService.parseTopQuotaOccupancyFileShares(this.shares);
      } catch (e) {
         this.errorMessage = e;
      } finally {
         this.loading = false;
      }
   }

   navigateToFileShares(): void {
      this.viewMoreCliked.emit(FileAnalyticsFileSharesListType.QUOTA_USAGE);
   }
}
