import {Component, Input, OnChanges, SimpleChanges} from "@angular/core";
import {DateRangeData} from "@component/datetime-picker/date-range-picker.component";
import {FileAnalyticsDashboardService} from "@generated/file-analytics-dashboard-service";
import {ManagedObject} from "@service/managed-object";
import {PaneType} from "@component/master-detail/slide-panel.component";
import {VsanFileShareProtocol} from "@generated/vsan-file-share-protocol";
import {Icon} from "@util/icon";
import {VsanUiUtils} from "@util/vsan-util";
import {FileAnalyticsFileSharesListType} from "@generated/file-analytics-file-shares-list-type";
import {RefreshHandler} from "@service/global-refresh.service";
import {SharesContext} from "@navigation/context/shares-context";
import {FileShareTypeFilterOptions} from "../../file-services/shares/filters/file-share-type-filter-options";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {FileShareGridUtil} from "./file-share-grid.util";
import {FileAnalyticsShareData} from "@generated/file-analytics-share-data";
import {FileAnalyticsUtil} from "../file-analytics-util";

@Component({
   selector: "vsan-file-analytics-shares-list",
   templateUrl: "file-analytics-shares-list.html",
   styleUrls: ["file-analytics-shares-list.scss"],
})
export class FileAnalyticsSharesListComponent implements OnChanges {

   @Input()
   reportType: FileAnalyticsFileSharesListType;

   @Input()
   dateRange: DateRangeData;

   fileSharesData: FileAnalyticsShareData[] = [];
   selectedShare: FileAnalyticsShareData;

   loading: boolean;
   errorMessage: string;

   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;
   VsanFileShareProtocol: typeof VsanFileShareProtocol = VsanFileShareProtocol;
   FileShareGridUtil: typeof FileShareGridUtil = FileShareGridUtil;
   FileAnalyticsUtil: typeof FileAnalyticsUtil = FileAnalyticsUtil;
   Icon: typeof Icon = Icon;

   private showDetailPanel: boolean;

   constructor(private dashboardService: FileAnalyticsDashboardService, private navigationService: NavigationService) {}

   ngOnChanges(change: SimpleChanges): void {
      this.fetch();
   }

   @RefreshHandler
   async fetch(): Promise<void> {
      try {
         this.loading = true;
         this.fileSharesData = await this.dashboardService.queryFileShares(
               ManagedObject.contextObject, this.reportType, this.dateRange.from, this.dateRange.to);
      } catch (err) {
         this.errorMessage = err;
      } finally {
         this.loading = false;
      }
   }

   get activePane(): PaneType {
      return this.showDetailPanel ? PaneType.RIGHT : PaneType.LEFT;
   }

   set activePane(pane: PaneType) {
      this.showDetailPanel = pane === PaneType.RIGHT;
   }

   isSelected(item: FileAnalyticsShareData): boolean {
      return this.selectedShare === item && this.showDetailPanel;
   }

   /**
    * toggle method to show/hide the details pane
    */
   toggle(item: FileAnalyticsShareData): void {
      if (this.selectedShare === item) {
         this.showDetailPanel = !this.showDetailPanel;
      } else {
         this.selectedShare = item;
         this.showDetailPanel = true;
      }
   }

   navigateToFileShares(fileShare: FileAnalyticsShareData): void {
      const fileServiceSharesContext: SharesContext = {
         preselectedShareName: fileShare.shareName,
         shareTypeFilter: FileShareTypeFilterOptions.FILE_SHARE,
      };
      this.navigationService.navigateTo(PlatformExtension.FILE_SERVICE_SHARES, null, fileServiceSharesContext);
   }
}
