import {Component, EventEmitter, Input, Output} from "@angular/core";
import {FileAnalyticsDashboardService} from "@generated/file-analytics-dashboard-service";
import {FileAnalyticsChartService} from "../file-analytics-chart.service";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {Logger} from "@util/logger";
import {DateRangeData} from "@component/datetime-picker/date-range-picker.component";
import {FileAnalyticsFastestGrowingShareData} from "@generated/file-analytics-fastest-growing-share-data";
import {ColumnLineChartData} from "@component/chart/column-line-chart/column-line-chart.data";
import {FileAnalyticsUtil} from "../file-analytics-util";
import {VsanUiUtils} from "@util/vsan-util";
import {FileAnalyticsFileSharesListType} from "@generated/file-analytics-file-shares-list-type";
import {IllustratedMessageType} from "@component/illustrated-message/illustrated-message.component";

@Component({
   templateUrl: "file-analytics-file-share-fastest-growing.html",
   styleUrls: ["file-analytics-file-share-fastest-growing.scss", "../file-analytics-common.scss"],
   selector: "vsan-file-analytics-file-share-fastest-growing",
})
export class FileAnalyticsFileShareFastestGrowingComponent {
   IllustratedMessageType: typeof IllustratedMessageType = IllustratedMessageType;

   private _dateRange: DateRangeData;

   @Input()
   set dateRange(range: DateRangeData) {
      this._dateRange = range;
      if (range) {
         this.fetch();
      }
   }

   get dateRange(): DateRangeData {
      return  this._dateRange;
   }

   @Output()
   viewMoreCliked: EventEmitter<FileAnalyticsFileSharesListType> = new EventEmitter<FileAnalyticsFileSharesListType>();

   loading: boolean = true;
   errorMessage: string;

   // Holds the line chart series data
   lines: ColumnLineChartData[] = [];

   // The xAxis chart labels, based on the query period as set by the dateRange input.
   categories: string[] = [];

   // Shows a subtitle in the chart, stating the period for which the chart's data is about.
   chartTitle: string;

   FileAnalyticsUtil: typeof FileAnalyticsUtil = FileAnalyticsUtil;

   constructor(private dashboardService: FileAnalyticsDashboardService,
         private fileAnalyticsChartService: FileAnalyticsChartService) {
   }

   @RefreshHandler
   async fetch(): Promise<void> {
      this.loading = true;
      this.errorMessage = null;
      this.prepareChart();
      try {
         const result: FileAnalyticsFastestGrowingShareData[] = await this.dashboardService.queryFastestGrowingShares(
               ManagedObject.contextObject, this.dateRange.from, this.dateRange.to);
         this.lines = this.fileAnalyticsChartService.parseFastestGrowingFileSharesData(result);
         this.categories = this.getChartCategoriesFromReportData(result);
      } catch (e) {
         Logger.error(e);
         this.errorMessage = e;
      } finally {
         this.loading = false;
      }
   }

   /**
    * Get the xAxis categories from the dates in the result
    */
   private getChartCategoriesFromReportData(reportData: FileAnalyticsFastestGrowingShareData[]): string[] {
      const categories: number[] = [];
      for (const report of reportData) {
         if (VsanUiUtils.isEmpty(report.growthData)) {
            continue;
         }
         for (const growthData of report.growthData) {
            categories.push(growthData.date);
         }
      }
      return FileAnalyticsUtil.getCategoryDates(categories);
   }

   navigateToFileShares(): void {
      this.viewMoreCliked.emit(FileAnalyticsFileSharesListType.FASTEST_GROWTH);
   }

   private prepareChart(): void {
      this.chartTitle = FileAnalyticsUtil.getChartSubTitleFromDateRange(this.dateRange);
   }
}
