import {Component, OnInit} from "@angular/core";
import {IllustratedMessageType} from "@component/illustrated-message/illustrated-message.component";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {AlertActionLink} from "@component/validation/alert-action-link";
import {AlertActionLinkUtil} from "@component/validation/alert-action-link-util";
import {AlertType} from "@component/validation/alert-type";
import {FileAnalyticsConfigService} from "@generated/file-analytics-config-service";
import {FileAnalyticsScanStatusInfo} from "@generated/file-analytics-scan-status-info";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {VsanFileServiceCommonConfig} from "@generated/vsan-file-service-common-config";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {ClusterComputeResource} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {TaskMonitorService} from "@service/task-monitor-service";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";
import {FileServiceUtil} from "../file-services/file-service-util";

@Component({
   templateUrl: "file-analytics.html",
   styleUrls: ["file-analytics.scss"],
   selector: "vsan-file-analytics",
})
export class FileAnalyticsComponent implements OnInit {
   FileAnalyticsView: typeof FileAnalyticsView = FileAnalyticsView;
   IllustratedMessageType: typeof IllustratedMessageType = IllustratedMessageType;
   AlertType: typeof AlertType = AlertType;

   loadingConfig: boolean = true;
   loadingLastScan: boolean = true;
   sharesLoading: boolean = true;
   errorMessages: string[] = [];
   currentView: FileAnalyticsView = FileAnalyticsView.FILE_DASHBOARD;

   clusterRef: ManagedObject = ManagedObject.contextObject;
   clusterName: string;
   hasEditPrivilege: boolean = false;
   scanStatus: FileAnalyticsScanStatusInfo;
   isFileAnalyticsEnabled: boolean = false;
   isFileAnalyticsRuntimeSupported: boolean = false;
   isPerformanceServiceEnabled: boolean = true;
   fileAnalyticsNotEnabledView: UnavailableViewSpec;
   fileAnalyticsNoDataView: UnavailableViewSpec;
   fileAnalyticsNoFileSharesView: UnavailableViewSpec;

   // File shares in the current cluster. We need to check the shares to display the unavailable view
   fileShares: VsanFileServiceShare[] = [];
   selectedShares: VsanFileServiceShare[] = [];

   enablePerformanceServiceAction: AlertActionLink;

   constructor(private fileAnalyticsConfigService: FileAnalyticsConfigService,
         private fileServiceConfigService: VsanFileServiceConfigService,
         private dataService: DataServiceClient,
         private taskService: TaskMonitorService,
         private permissionService: PermissionService) {
   }

   async ngOnInit(): Promise<void> {
      this.permissionService
            .hasPermissions(this.clusterRef, [Privileges.EDIT_CLUSTER()])
            .then(hasEditClusterPermission => this.hasEditPrivilege = hasEditClusterPermission)
            .catch(e => {
               Logger.error("Error fetching cluster priviledges: " + e);
               this.hasEditPrivilege = true;
            });
      await this.fetch();
   }

   @RefreshHandler
   async fetch(): Promise<void> {
      this.loadingConfig = true;
      this.loadingLastScan = true;
      this.reset();
      try {
         this.queryClusterName();
         await this.fetchConfigData();
         if (!this.isServiceEnabled) {
            // If file analytics is not enabled, we won't fetch the last scan result, so set loading to false;
            this.loadingLastScan = false;
            this.fileAnalyticsNotEnabledView = UnavailableViewSpec.FILE_ANALYTICS_DISABLED;
            if (!this.isFileAnalyticsRuntimeSupported) {
               // Disable the file analytics enablement if it's not supported from the cluster.
               this.fileAnalyticsNotEnabledView
                     .disableActionButton(VsanUiUtils.getString("vsan.cluster.dialog.file.analytics.unsupported"));
            }
         }
      } catch (e) {
         this.errorMessages.push(e);
      } finally {
         this.loadingConfig = false;
      }
   }

   private reset(): void {
      this.errorMessages = [];
      this.isPerformanceServiceEnabled = false;
      this.isFileAnalyticsEnabled = false;
      this.isFileAnalyticsRuntimeSupported = false;
      this.scanStatus = null;
      this.fileShares = [];
      this.fileAnalyticsNotEnabledView = null;
      this.fileAnalyticsNoDataView = null;
      this.fileAnalyticsNoFileSharesView = null;
   }

   private async fetchConfigData(): Promise<void> {
      try {
         const vsanFileServiceCommonConfig: VsanFileServiceCommonConfig = await
               this.fileAnalyticsConfigService.getFileAnalyticsConfigData(this.clusterRef);
         this.isFileAnalyticsEnabled = vsanFileServiceCommonConfig.isFileAnalyticsEnabled;
         this.isPerformanceServiceEnabled = vsanFileServiceCommonConfig.isPerformanceServiceEnabled;
         this.enablePerformanceServiceAction = AlertActionLinkUtil.getPerformanceServiceActionLink(
               {
                  clusterRef: this.clusterRef,
                  clusterName: this.clusterName,
               },
               this.awaitThenReload);
         if (!this.isFileAnalyticsEnabled) {
            // Get the cluster support status for the file analytics feature.
            // Based on this property, the feature enablement is either allowed or not.
            this.isFileAnalyticsRuntimeSupported =
                  await this.fileAnalyticsConfigService.getIsFileAnalyticsRuntimeSupported(this.clusterRef);
         }
      } catch (e) {
         Logger.error("Error fetching file service configuration: " + e);
         this.errorMessages.push(e);
      }
   }

   private async queryClusterName(): Promise<void> {
      try {
         this.clusterName = await this.dataService.getProperty(ClusterComputeResource.name, this.clusterRef);
      } catch (e) {
         Logger.error("Error fetching the cluster's name: " + e);
         this.errorMessages.push(e);
      }
   }

   updateDashboard(scanStatus: FileAnalyticsScanStatusInfo): void {
      this.scanStatus = scanStatus;
      this.loadingLastScan = false;
      if (!this.scanStatus) {
         this.fileAnalyticsNoDataView = UnavailableViewSpec.FILE_ANALYTICS_NO_DATA;
         // Shares data will not be queried without scan status, set to false to hide the spinner
         this.sharesLoading = false;
         return;
      }
      this.queryFileShares();
   }

   fileSharesSelected(shares: VsanFileServiceShare[]): void {
      if (VsanUiUtils.isEmpty(shares)) {
         this.selectedShares = [];
      } else {
         // Create new array to trigger change detection in child views
         this.selectedShares = [...shares];
      }
   }

   async queryFileShares(): Promise<void> {
      // Only query the file shares and reports data if the file analytics and performance service are enabled.
      if (!this.isServiceEnabled || !this.scanStatus) {
         return;
      }
      try {
         this.sharesLoading = true;
         this.fileShares = await this.fileServiceConfigService.getFileShareAttributes(this.clusterRef, [
            FileServiceUtil.SHARE_NAME_PROPERTY,
            FileServiceUtil.SHARE_UUID_PROPERTY,
            FileServiceUtil.SHARE_FILE_INDEXING_ENABLED_PROPERTY]);
         if (!this.fileAnalyticsEnabledSharesExist()) {
            this.fileAnalyticsNoFileSharesView = UnavailableViewSpec.FILE_ANALYTICS_NO_SHARE_ENABLED;
         }
      } catch (e) {
         Logger.error("Error fetching file shares data: " + e);
         this.errorMessages.push(e);
      } finally {
         this.sharesLoading = false;
      }
   }

   fileAnalyticsEnabledSharesExist(): boolean {
      return this.fileShares.some(share => share.config.isFileAnalyticsEnabled);
   }

   get isServiceEnabled(): boolean {
      return this.isFileAnalyticsEnabled && this.isPerformanceServiceEnabled;
   }

   private awaitThenReload = (taskRef: ManagedObject) => {
      this.taskService.getAwaitResult(taskRef)
            .then(() => this.fetch())
            .catch(e => this.errorMessages.push(e));
   };
}

export enum FileAnalyticsView {
   FILE_DASHBOARD,
   FILE_SHARE_DASHBOARD,
}
