import {AfterViewInit, Component, ElementRef, Input, OnChanges, SimpleChanges, ViewChild} from "@angular/core";
import {ReferenceWatcher} from "@util/reference-watcher";
import {HighchartsThemeService} from "@component/chart/highcharts-theme.service";
import {HighchartsPropertiesService} from "@component/chart/highcharts-properties.service";
import {VsanUiUtils} from "@util/vsan-util";
import {ColumnLineChartData} from "@component/chart/column-line-chart/column-line-chart.data";
import * as Highcharts from "highcharts";
import {HighchartsTooltipUtil} from "@component/chart/highcharts-tooltip.util";

@Component({
   selector: "vsan-column-line-chart",
   templateUrl: "column-line-chart.html",
   styleUrls: ["column-line-chart.scss"],
   providers: [ReferenceWatcher],
})
export class ColumnLineChartComponent implements AfterViewInit, OnChanges {

   /**
    * Holds the lines series data.
    */
   @Input()
   lines: ColumnLineChartData[];

   /**
    * Holds the columns series data.
    */
   @Input()
   columns: ColumnLineChartData[];

   /**
    * Holds the xAxis category values corresponding to the ColumnLineChartData.values.
    */
   @Input()
   categories: string[];

   /**
    * The title associated with the values.
    * Will be shown on the left side of the chart. E.g. yAxis title.
    */
   @Input()
   valuesTitle: string;

   /**
    * Custom formatter to be used to override the default yAxis label values.
    */
   @Input()
   valuesLabelFormatter: (value: number) => string;

   /**
    * Whether or not the legend entries can be clicked in order to hide/show individual series.
    */
   @Input()
   enableLegendInteraction: boolean = false;

   /**
    * In case a subtitle should be displayed for the line chart.
    */
   @Input()
   subtitle: string;

   @ViewChild("chart")
   chartElement: ElementRef;
   chart: Highcharts.Chart;

   private highchartOptions: any;

   private originalLegendItemClick: any;

   constructor(private highchartsService: HighchartsPropertiesService,
         private highchartsThemeService: HighchartsThemeService,
         private refWatcher: ReferenceWatcher) {
      this.refWatcher.watchSubscriptionRef(
            this.highchartsThemeService.whenThemeChanged.subscribe(theme => this.onThemeChanged()));
   }

   ngAfterViewInit(): void {
      this.render();
   }

   ngOnChanges(changes: SimpleChanges): void {
      this.applyHighchartOptions();
      this.populateHighchartSeries();
      this.render();
   }

   private onThemeChanged(): void {
      this.applyHighchartOptions();
      this.update();
   }

   private applyHighchartOptions(): void {
      if (VsanUiUtils.isEmpty(this.lines) && VsanUiUtils.isEmpty(this.columns)) {
         return;
      }
      this.highchartOptions = this.highchartsService.getColumnLineChartOptions();
      this.highchartOptions.xAxis.categories = this.categories;
      if (this.valuesTitle) {
         this.highchartOptions.yAxis.title.text = this.valuesTitle;
      }
      if (this.valuesLabelFormatter) {
         this.highchartOptions.yAxis.labels.formatter = this.valuesLabelFormatter;
      }
      if (this.subtitle) {
         this.highchartOptions.subtitle.text = this.subtitle;
      }
      if (this.enableLegendInteraction) {
         this.originalLegendItemClick = this.highchartOptions.plotOptions.series.events.legendItemClick;
         delete this.highchartOptions.plotOptions.series.events.legendItemClick;
      } else {
         if (this.originalLegendItemClick) {
            // restore the original click handler
            this.highchartOptions.plotOptions.series.events.legendItemClick = this.originalLegendItemClick;
            this.originalLegendItemClick = null;
         }
      }
   }

   private populateHighchartSeries(): void {
      if (!this.highchartOptions) {
         return;
      }
      const seriesData: any[] = this.getColumnLinechartSeriesData(this.lines);
      if (!VsanUiUtils.isEmpty(this.columns)) {
         seriesData.push(...this.getColumnLinechartSeriesData(this.columns, HighchartsPropertiesService.COLUMN_CHART));
      }
      this.highchartOptions.series = seriesData;
   }

   private getColumnLinechartSeriesData(seriesData: ColumnLineChartData[],
         type: string = HighchartsPropertiesService.LINE_CHART): any[] {
      if (!seriesData) {
         return [];
      }
      return seriesData.map((columnLineChartData: ColumnLineChartData) => columnLineChartData.toSeries(type));
   }

   private render(): void {
      if (!this.chartElement || !this.highchartOptions) {
         return;
      }
      this.highchartOptions.chart.renderTo = this.chartElement.nativeElement;
      this.chart = Highcharts.chart(this.highchartOptions);
      this.highchartsService.applyScreenReaderMessage(this.chartElement.nativeElement);
      this.refWatcher.watchChartRef(this.chart);
      // The chartData is needed for QE tests, related to charts verification
      this.attachTestingData();
   }

   private update(): void {
      if (this.chart) {
         this.chart.update(this.highchartOptions);
      }
   }

   private attachTestingData(): void {
      const testData: any = {
         lines: this.lines,
         columns: this.columns,
         categories: this.categories,
      };

      // We have the tooltip data in every ColumnLineChart object, so take it from the first available one.
      if (!VsanUiUtils.isEmpty(this.lines)) {
         testData.tooltipData = HighchartsTooltipUtil.getColumnLineTestingTooltipData(this.lines);
      }
      this.chartElement.nativeElement.chartData = testData;
   }
}
