import {
   ChangeDetectionStrategy,
   Component,
   ElementRef,
   EventEmitter,
   HostListener,
   Input,
   Output,
   ViewChild} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {Icon} from "@util/icon";

/**
 * Component that allows user to select multiple file shares,
 * it emits the selected shares when user check/uncheck the file share.
 */
@Component({
   selector: "vsan-multiple-file-share-selector",
   templateUrl: "multiple-file-share-selector.html",
   styleUrls: ["multiple-file-share-selector.scss"],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MultipleFileShareSelectorComponent {
   private _shares: VsanFileServiceShare[];

   @Input()
   set shares(items: VsanFileServiceShare[]) {
      this._shares = items;
      if (VsanUiUtils.isEmpty(items)) {
         return;
      }
      this.prepareSelectorAttributes();
      this.onSharesSelected.emit(this.selectedShares);
   }

   get shares(): VsanFileServiceShare[] {
      return this._shares;
   }

   @Output()
   onSharesSelected: EventEmitter<VsanFileServiceShare[]> = new EventEmitter<VsanFileServiceShare[]>(true);

   @ViewChild("fileShareDropdownElement")
   private fileShareDropdownElement: ElementRef;

   isDropdownOpened: boolean = false;
   selectedShares: VsanFileServiceShare[] = [];
   // The readable text of the share selector. It changes when user selection changes.
   readableSelectorLabel: string;
   analyticsEnabledShares: VsanFileServiceShare[];
   analyticsDisabledShares: VsanFileServiceShare[];
   selectAllIndeterminateState: boolean;
   Icon: typeof Icon = Icon;

   selectAllShares(selected: boolean): void {
      this.selectAllIndeterminateState = false;
      if (selected) {
         this.selectedShares = [...this.analyticsEnabledShares];
      } else {
         this.selectedShares = [];
      }
      this.readableSelectorLabel = this.getSelectorLabel();
      this.onSharesSelected.emit(this.selectedShares);
   }

   selectShare(share: VsanFileServiceShare, selected: boolean): void {
      if (selected) {
         this.selectedShares.push(share);
      } else {
         this.selectedShares = this.selectedShares.filter(selectedShare => selectedShare.uuid !== share.uuid);
      }
      this.selectAllIndeterminateState = !VsanUiUtils.isEmpty(this.selectedShares) && !this.areAllSharesSelected();
      this.readableSelectorLabel = this.getSelectorLabel();

      this.onSharesSelected.emit(this.selectedShares);
   }

   areAllSharesSelected(): boolean {
      return !VsanUiUtils.isEmpty(this.selectedShares)
            && this.selectedShares.length === this.analyticsEnabledShares.length;
   }

   isShareSelected(share: VsanFileServiceShare): boolean {
      return this.selectedShares.some(selectedShare => selectedShare.uuid === share.uuid);
   }

   // Close the dropdown menu when the user clicks outside of the menu.
   // Clarity only closes the menus automatically when using the standard mode(single selection,
   // button as the dropdown item, ..). Here we are using the html version(not clr-dropdown),
   // and we have customization on the dropdown items to support multi-selection, have to close it by our own.
   @HostListener("document:click", ["$event"])
   clickout(event: MouseEvent): void {
      if (this.isDropdownOpened &&
            !this.fileShareDropdownElement.nativeElement.contains(event.target)) {
         this.isDropdownOpened = false;
      }
   }

   toggleDropdown(): void {
      this.isDropdownOpened = !this.isDropdownOpened;
   }

   private getSelectorLabel(): string {
      if (VsanUiUtils.isEmpty(this.selectedShares)) {
         return VsanUiUtils.getString("vsan.cluster.monitor.file.analytics.files.share.selector.empty.selection");
      }
      if (this.areAllSharesSelected()) {
         return VsanUiUtils.getString("vsan.cluster.monitor.file.analytics.files.share.selector.all");
      }
      if (this.selectedShares.length > 1) {
         return VsanUiUtils.getString(
               "vsan.cluster.monitor.file.analytics.files.share.selector.partial.plural", this.selectedShares.length);
      }
      return this.selectedShares[0].config.name;
   }

   private prepareSelectorAttributes(): void {
      this.analyticsEnabledShares = this.shares.filter(share => share.config.isFileAnalyticsEnabled);
      this.analyticsDisabledShares = this.shares.filter(share => !share.config.isFileAnalyticsEnabled);

      // By default we should select all the available shares
      this.selectedShares = [...this.analyticsEnabledShares];

      this.readableSelectorLabel = this.getSelectorLabel();
   }
}
