import {Directive, ElementRef, Input, OnInit} from "@angular/core";
import {ReferenceWatcher} from "@util/reference-watcher";
import {HighchartsThemeService} from "@component/chart/highcharts-theme.service";
import {ThemeSpecificColor} from "@model/theme-specific-color";
import {VsanUiUtils} from "@util/vsan-util";
import {Logger} from "@util/logger";
import {ClarityColors} from "@util/clarity-colors";

/**
 * Use vsan-barchart to draw a custom simple barchart.
 * Input values are:
 * - percentage [from 0 to 100] used for barchart relative width based on the host parent width
 * - height [use VsanBarchartHeight predefined heights in rems] used for bar height
 * - backgroundColor [use ThemeSpecificColor to specify theme specific color]
 * Example:
 * <div class="example-container">
 *    <div vsan-barchart
 *         [percentage]="getPercentage([from 0 to 100])"
 *         [height]="[VsanBarchartHeight.SMALL, VsanBarchartHeight.MEDIUM or VsanBarchartHeight.LARGE]"
 *         [backgroundColor]="[color (#0065ab)]"></div>
 * </div>
 */
@Directive({
   selector: "[vsan-barchart]",
   host: {
      "[style.width]": "percentage + '%'",
      "[style.height]": "height + 'rem'",
      "[style.background-color]": "appliedBackgroundColor",
      "[style.opacity]": "opacity",
   },
   providers: [ReferenceWatcher],
})
export class VsanBarchartDirective implements OnInit {
   private _percentage: number;

   /**
    * Barchart width in percentage based on the host element width
    */
   @Input()
   set percentage(value: number) {
      if (!VsanUiUtils.isPercentage(value)) {
         Logger.error(`Incorrect percentage value ${value} for vsan-barchart directive.`);
         return;
      }
      this._percentage = value;
   }

   get percentage(): number {
      return this._percentage;
   }

   /**
    * Barchart height in rems. Use predefined heights in VsanBarchartHeight
    */
   @Input()
   height: VsanBarchartHeight = VsanBarchartHeight.MEDIUM;

   /**
    * The background color of the barchart. Uses blue color by default.
    */
   @Input()
   backgroundColor: ThemeSpecificColor = ClarityColors.BLUE;

   appliedBackgroundColor: string;

   /**
    * Used to set some transparency of the barchart color
    * The opacity range is from 0 to 1
    */
   @Input()
   opacity: number = 1;

   constructor(private refWatcher: ReferenceWatcher,
         private highchartsThemeService: HighchartsThemeService,
         private el: ElementRef<HTMLElement>) {}

   ngOnInit(): void {
      // Set the theme specific color, based on current applied theme
      this.changeColors();
      // Listen for theme change
      this.refWatcher.watchSubscriptionRef(
            this.highchartsThemeService.whenThemeChanged.subscribe(this.changeColors));

      if (this.percentage > 0 && this.el) {
         // Use min width to avoid invisible charts, when the percentage is too low.
         this.el.nativeElement.style.minWidth = "2px";
      }
   }

   private changeColors = () => {
      this.appliedBackgroundColor = this.backgroundColor.getColor();
   };
}

/**
 * Predefined heights in rems for barchart directive.
 */
export enum VsanBarchartHeight {
   SMALL = 0.3,
   MEDIUM= 0.6,
   LARGE = 1.2,
}